@charset "UTF-8";
/* Slider */
/* Icons */
@font-face {
   font-family: "slick";
   src: url("../../../fonts/slick.eot");
   src: url("../../../fonts/slick-1.eot") format("embedded-opentype"),
      url("../../../fonts/slick.woff") format("woff"),
      url("../../../fonts/slick.ttf") format("truetype"),
      url("../../../fonts/slick.svg") format("svg");
   font-weight: normal;
   font-style: normal;
}

/* Arrows */
.slick-prev,
.slick-next {
   position: absolute;
   display: block;
   height: 20px;
   width: 20px;
   line-height: 0px;
   font-size: 0px;
   cursor: pointer;
   background: transparent;
   color: transparent;
   top: 50%;
   -webkit-transform: translateY(-50%);
   transform: translateY(-50%);
   padding: 0;
   border: none;
   outline: none;
}
.slick-prev:hover,
.slick-prev:focus,
.slick-next:hover,
.slick-next:focus {
   outline: none;
   background: transparent;
   color: transparent;
}
.slick-prev:hover:before,
.slick-prev:focus:before,
.slick-next:hover:before,
.slick-next:focus:before {
   opacity: 1;
}
.slick-prev.slick-disabled:before,
.slick-next.slick-disabled:before {
   opacity: 0.25;
}
.slick-prev:before,
.slick-next:before {
   font-family: "slick";
   font-size: 20px;
   line-height: 1;
   color: white;
   opacity: 0.75;
   -webkit-font-smoothing: antialiased;
   -moz-osx-font-smoothing: grayscale;
}

.slick-prev {
   left: -25px;
}
.slick-prev:before {
   content: "←";
}

.slick-next {
   right: -25px;
}
.slick-next:before {
   content: "→";
}

/* Dots */
.slick-dotted.slick-slider {
   margin-bottom: 30px;
}

.slick-dots {
   position: absolute;
   left: 50%;
   -webkit-transform: translateX(-50%);
   transform: translateX(-50%);
   list-style: none;
   text-align: center;
   padding: 0;
   margin: 0;
}
.slick-dots li {
   position: relative;
   display: inline-block;
   height: 20px;
   width: 20px;
   margin: 0 5px;
   padding: 0;
   cursor: pointer;
}
.slick-dots li button {
   border: 0;
   background: transparent;
   display: block;
   height: 20px;
   width: 20px;
   outline: none;
   line-height: 0px;
   font-size: 0px;
   color: transparent;
   padding: 5px;
   cursor: pointer;
}
.slick-dots li button:hover,
.slick-dots li button:focus {
   outline: none;
}
.slick-dots li button:hover:before,
.slick-dots li button:focus:before {
   opacity: 1;
}
.slick-dots li button:before {
   position: absolute;
   top: 0;
   left: 0;
   content: "•";
   width: 20px;
   height: 20px;
   font-family: "slick";
   font-size: 6px;
   line-height: 20px;
   text-align: center;
   color: black;
   opacity: 0.25;
   -webkit-font-smoothing: antialiased;
   -moz-osx-font-smoothing: grayscale;
}
.slick-dots li.slick-active button:before {
   color: black;
   opacity: 0.75;
}
